import 'focus-visible';
import React from 'react';
import { BaseButton, DEFAULT_PROPS as DEFAULT_PROPS_BASE_BUTTON } from 'shared/BaseButton';
import { ButtonExternalSize, ButtonExternalType, ShareableButtonProps } from 'shared/types/button';

import styles from 'shared/BaseButton/externalStyles.scss';

export const DEFAULT_PROPS_EXT_BUTTON = {
  type: 'primary',
  size: 'default'
};

export interface ButtonProps extends Omit<ShareableButtonProps, 'isIcon'> {
  /** Контект кнопки */
  text: string;

  /** Тип кнопки */
  type?: ButtonExternalType;

  /** Размер кнопки */
  size?: ButtonExternalSize;

  /** Пропс для растягивания кнопки на ширину родительского контейнера */
  stretch?: boolean;

  /** Флаг, акцентное отображение кнопки для Secondary и Tertairy варианта отображения */
  accent?: boolean;

  /** Флаг траурного режима */
  isMourning?: boolean;
}

export const Button: React.FC<ButtonProps> = ({
  text,
  ...props
}) => (
  <BaseButton
    { ...props }
    styles={ styles }
    content={ text }
  />
);

Button.defaultProps = {
  ...DEFAULT_PROPS_EXT_BUTTON,
  ...DEFAULT_PROPS_BASE_BUTTON
} as ButtonProps;
